.landingPageBox{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  margin: 3% 3% 0% 3%;
}

p{
  font-size: 18px;
}

/* Media queries */

/* Desktop */
@media (min-width: 900px){
  p{
    font-size: 40px;
  }

  .landingPageBox{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    margin: 0% 10% 0% 10%;
  }
  .casePageBox{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    margin: 0% 10% 0% 10%;
  }
}

/* tablet */
@media (min-width: 600px){
  p{
    font-size: 28px;
  }
  .casePageBox{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    margin: 0% 10% 0% 10%;
  }
}



